<template>
  <div class="room-details-wrapper">
      {{ roominfo.id }}
      {{ roominfo.price }}
      {{ roominfo.id }}
  </div>
</template>
<script>
import {APIGetroom} from '../../api/api'
export default {
  data () {
      return {
          roominfo:{}
      }
  },
  methods: {
      Showroomdetails(){
          APIGetroom(
              this.$route.params.id
          ).then( res => {
              this.roominfo = res.data
            //   console.log(res.data)
          }).catch( err => { console.log(err) })
      }

  },
  created(){
      this.Showroomdetails()
  }
}
</script>




